  bottom: 0;
  right: 0;
}
.visual-editor .v-scroll-bar .inner-bar {
  width: 0.5rem;
  height: 5rem;
}
.visual-editor .h-scroll-bar {
  border-top: 1px solid gray;
  height: 0.5rem;
  left: 1rem;
  bottom: 0;
  right: 0.5rem;
}
.visual-editor .h-scroll-bar .inner-bar {
  height: 0.5rem;
  width: 5rem;
}
.visual-editor .panel-main {
  border: none;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #fff;
}
.visual-editor .panel-main::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
.visual-editor .panel-main::-webkit-scrollbar-track {
  -webkit-border-radius: 2em;
  -moz-border-radius: 2em;
  border-radius: 2em;
}
.visual-editor .panel-main::-webkit-scrollbar-thumb {
  background-color: #aaa;
