  position: absolute;
}
.visual-editor .h-rule-bar {
  left: 1rem;
  top: 0;
  right: 0.5rem;
  height: 1rem;
}
.visual-editor .v-rule-bar {
  left: 0;
  top: 1rem;
  bottom: 0.5rem;
  width: 1rem;
}
.visual-editor .rule-line-bar .h-line,
.visual-editor .rule-line-bar .v-line {
  border-top: 1px dotted var(--zre--primary);
  border-right: 1px dotted var(--zre--primary);
